.smart-card{
    width: 26%;
    box-shadow: 4px 4px 4px rgba(21, 93, 248, 0.2);
    border-radius: 20px;
    margin-bottom: 40px;
}
.smart-icon{
    font-size: 50px;
    padding: 10px;
}
@media(max-width:991px){
    .smart-card{
        width: 40%;
    }
}
@media(max-width:678px){
    .smart-card{
        width: 80%;
        margin: auto;
        margin-bottom: 30px;
    }
}