.navbar{
    max-width: 80%;
    margin: auto;
    padding-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 2 !important;
}
.navbar-logo{
    font-size: bold;
    font-size: 25px;
    color: white;
}
.navbar-items .items-group{
    display: flex !important;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    transition: 1s all ease-in-out;
}
.nav-item{
    margin-left: 10px;
}
.nav-item a{
    color: white;
    transition: 0.5s all ease-in-out;
}
.nav-item a:hover{
    color: rgb(18, 0, 177);
    transition: 0.5s all ease-in-out;
}
.toggle-button{
    width: 40px;
    height: 45px;
    padding: 5px;
    display: none;
    flex-direction: column;
    justify-content: space-evenly;
    align-items:center;
    cursor: pointer;
}
.toggle-button span{
    display: block;
    width: 100%;
    height: 3px;
    background-color: white;
}
.toggle-button span:nth-child(1){
    margin-top: 15px;
    transform: rotate(50deg);
}
.toggle-button span:nth-child(2){
    margin-top: -13px;
    transform: rotate(-50deg);
}
.toggle-button span:nth-child(3){
 display: none;
}
@media(max-width:991px){
    .navbar-items .items-group{
        position: absolute;
        top: 70px;
        flex-direction: column;
        background-color: white;
        box-shadow: 4px 4px 4px rgba(0,0,0,.2);
        width: 300px;
        align-items: start;
        padding: 5px;
        transition: 1s all ease-in-out;
    }
    .nav-item{
        border-bottom: 1px solid rgb(214, 214, 214);
        width: 90%;
        margin-bottom: 10px;
    }
    .nav-item a{
        color: rgb(145, 145, 145);
    }
    .toggle-button{
        display: flex;
    }
}