.cardHeader {
    text-align: center;
    background-size: cover;
    border-radius: 20px;
    margin-bottom: 40px;
}
.card-detail{
    position: absolute;
    width: 100%;
    bottom: -20px;
    text-align: center;
}
@media(max-width:991px){
    .cardHeader {
        width: 40% !important;
    }
}
@media(max-width:678px){
    .cardHeader {
        width: 80% !important;
    }
}