.header{
    border-bottom-right-radius: 100px;
    border-bottom-left-radius: 100px;
    background-size: cover;
    overflow:hidden ;
}
.overly-header{
    position: absolute;
    top: 0px;
    right: 0px;
    width: 100%;
    height: 100%;
    background-color: #0c2d86;
    opacity: 0.2;
    border-bottom-right-radius: 100px;
    border-bottom-left-radius: 100px;
}
.header-caption{
    position: relative;
    width: 80%;
    margin: auto;
    text-align: center;
    margin-top: 150px;
    padding-bottom: 150px;
}
.header-caption h1{
    font-size: 30px;
    color: white;
    margin-bottom: 10px;
}
.Header-details{
    margin-top: 100px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
@media(max-width:991px){
    .header-caption{
        margin-top: 50px;
        padding-bottom: 50px;
    }
}